"use client";

import type React from "react";

import { useState } from "react";
import { motion } from "framer-motion";
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Brain, Code, Palette, BookOpen, Heart, Briefcase } from "lucide-react";

interface Personality {
  id: string;
  name: string;
  description: string;
  traits: string[];
  icon: React.ReactNode;
  color: string;
  bgColor: string;
  example: string;
}

export default function AIPersonalitySelector() {
  const [selectedPersonality, setSelectedPersonality] = useState<string | null>(
    null
  );

  const personalities: Personality[] = [
    {
      id: "analytical",
      name: "The Analyst",
      description: "Logical, data-driven, and precise in every response",
      traits: ["Logical", "Precise", "Data-driven", "Methodical"],
      icon: <Brain className="h-6 w-6" />,
      color: "text-blue-600",
      bgColor: "bg-blue-50",
      example:
        "Based on the data patterns, I recommend a systematic approach with 3 key steps...",
    },
    {
      id: "creative",
      name: "The Creator",
      description: "Imaginative, artistic, and full of innovative ideas",
      traits: ["Imaginative", "Artistic", "Innovative", "Expressive"],
      icon: <Palette className="h-6 w-6" />,
      color: "text-purple-600",
      bgColor: "bg-purple-50",
      example:
        "Let's paint this idea with bold strokes! Imagine a world where...",
    },
    {
      id: "technical",
      name: "The Engineer",
      description: "Technical expert with deep knowledge of systems and code",
      traits: ["Technical", "Systematic", "Problem-solver", "Efficient"],
      icon: <Code className="h-6 w-6" />,
      color: "text-green-600",
      bgColor: "bg-green-50",
      example:
        "Here's the optimal solution with O(n) complexity and minimal memory usage...",
    },
    {
      id: "friendly",
      name: "The Companion",
      description: "Warm, empathetic, and always ready to help",
      traits: ["Empathetic", "Supportive", "Warm", "Understanding"],
      icon: <Heart className="h-6 w-6" />,
      color: "text-pink-600",
      bgColor: "bg-pink-50",
      example:
        "I understand this might be challenging. Let's work through this together step by step!",
    },
    {
      id: "business",
      name: "The Strategist",
      description:
        "Business-focused with strategic thinking and market insights",
      traits: ["Strategic", "Business-savvy", "Goal-oriented", "Practical"],
      icon: <Briefcase className="h-6 w-6" />,
      color: "text-orange-600",
      bgColor: "bg-orange-50",
      example:
        "From a strategic perspective, this opportunity has 3 key advantages...",
    },
    {
      id: "educator",
      name: "The Teacher",
      description: "Patient educator who breaks down complex topics",
      traits: ["Patient", "Clear", "Educational", "Encouraging"],
      icon: <BookOpen className="h-6 w-6" />,
      color: "text-indigo-600",
      bgColor: "bg-indigo-50",
      example:
        "Great question! Let me explain this concept step by step with examples...",
    },
  ];

  return (
    <div className="max-w-6xl mx-auto">
      <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
        {personalities.map((personality, index) => (
          <motion.div
            key={personality.id}
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: index * 0.1 }}
            whileHover={{ y: -5 }}
          >
            <Card
              className={`p-6 cursor-pointer transition-all duration-300 border-2 ${
                selectedPersonality === personality.id
                  ? "border-sky-500 shadow-xl scale-105"
                  : "border-gray-200 hover:border-gray-300 hover:shadow-lg"
              }`}
              onClick={() => setSelectedPersonality(personality.id)}
            >
              {/* Header */}
              <div className="flex items-center space-x-3 mb-4">
                <div
                  className={`p-3 rounded-lg ${personality.bgColor} ${personality.color}`}
                >
                  {personality.icon}
                </div>
                <div>
                  <h3 className="text-lg font-semibold text-gray-800">
                    {personality.name}
                  </h3>
                  {selectedPersonality === personality.id && (
                    <motion.div
                      initial={{ scale: 0 }}
                      animate={{ scale: 1 }}
                      className="text-sm text-sky-600 font-medium"
                    >
                      Selected ✓
                    </motion.div>
                  )}
                </div>
              </div>

              {/* Description */}
              <p className="text-gray-600 text-sm mb-4">
                {personality.description}
              </p>

              {/* Traits */}
              <div className="flex flex-wrap gap-2 mb-4">
                {personality.traits.map((trait) => (
                  <Badge key={trait} variant="secondary" className="text-xs">
                    {trait}
                  </Badge>
                ))}
              </div>

              {/* Example */}
              <div
                className={`p-3 rounded-lg ${personality.bgColor} border-l-4 border-current ${personality.color}`}
              >
                <p className="text-xs text-gray-600 mb-1">Example response:</p>
                <p className="text-sm text-gray-800 italic">
                  "{personality.example}"
                </p>
              </div>

              {/* Action Button */}
              <div className="mt-4">
                <Button
                  variant={
                    selectedPersonality === personality.id
                      ? "default"
                      : "outline"
                  }
                  size="sm"
                  className="w-full"
                  onClick={(e) => {
                    e.stopPropagation();
                    setSelectedPersonality(personality.id);
                  }}
                >
                  {selectedPersonality === personality.id
                    ? "Selected"
                    : "Try This Personality"}
                </Button>
              </div>
            </Card>
          </motion.div>
        ))}
      </div>

      {/* Selected Personality Preview */}
      {selectedPersonality && (
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          className="mt-8"
        >
          <Card className="p-6 bg-gradient-to-r from-sky-50 to-blue-50 border-sky-200">
            <div className="text-center">
              <h3 className="text-xl font-semibold text-gray-800 mb-2">
                You've selected:{" "}
                <span className="text-sky-600">
                  {
                    personalities.find((p) => p.id === selectedPersonality)
                      ?.name
                  }
                </span>
              </h3>
              <p className="text-gray-600 mb-4">
                Your AI will now respond with this personality. You can change
                it anytime!
              </p>
              <Button className="bg-sky-600 hover:bg-sky-700">
                Start Chatting with Your AI
              </Button>
            </div>
          </Card>
        </motion.div>
      )}
    </div>
  );
}
