"use client";

import type React from "react";

import { useState, useEffect } from "react";
import { motion } from "framer-motion";
import { Card } from "@/components/ui/card";
import {
  Brain,
  Zap,
  Globe,
  Users,
  Activity,
  Cpu,
  Database,
  Network,
} from "lucide-react";

interface MetricData {
  label: string;
  value: string;
  change: string;
  icon: React.ReactNode;
  color: string;
}

export default function RealTimeAIMetrics() {
  const [metrics, setMetrics] = useState<MetricData[]>([
    {
      label: "AI Queries/sec",
      value: "1,247",
      change: "+12%",
      icon: <Brain className="h-6 w-6" />,
      color: "text-blue-500",
    },
    {
      label: "Response Time",
      value: "0.3s",
      change: "-5%",
      icon: <Zap className="h-6 w-6" />,
      color: "text-green-500",
    },
    {
      label: "Active Models",
      value: "47",
      change: "+3",
      icon: <Cpu className="h-6 w-6" />,
      color: "text-purple-500",
    },
    {
      label: "Global Nodes",
      value: "156",
      change: "+8",
      icon: <Globe className="h-6 w-6" />,
      color: "text-orange-500",
    },
    {
      label: "Live Users",
      value: "12,847",
      change: "+247",
      icon: <Users className="h-6 w-6" />,
      color: "text-sky-500",
    },
    {
      label: "Model Accuracy",
      value: "99.7%",
      change: "+0.2%",
      icon: <Activity className="h-6 w-6" />,
      color: "text-emerald-500",
    },
    {
      label: "Data Processed",
      value: "2.4TB",
      change: "+156GB",
      icon: <Database className="h-6 w-6" />,
      color: "text-indigo-500",
    },
    {
      label: "Network Load",
      value: "67%",
      change: "-3%",
      icon: <Network className="h-6 w-6" />,
      color: "text-pink-500",
    },
  ]);

  // Simulate real-time updates
  useEffect(() => {
    const interval = setInterval(() => {
      setMetrics((prev) =>
        prev.map((metric) => ({
          ...metric,
          value: generateRandomValue(metric.label),
          change: generateRandomChange(),
        }))
      );
    }, 3000);

    return () => clearInterval(interval);
  }, []);

  const generateRandomValue = (label: string): string => {
    switch (label) {
      case "AI Queries/sec":
        return `${Math.floor(Math.random() * 500 + 1000).toLocaleString()}`;
      case "Response Time":
        return `${(Math.random() * 0.5 + 0.1).toFixed(1)}s`;
      case "Active Models":
        return `${Math.floor(Math.random() * 10 + 45)}`;
      case "Global Nodes":
        return `${Math.floor(Math.random() * 20 + 150)}`;
      case "Live Users":
        return `${Math.floor(Math.random() * 2000 + 12000).toLocaleString()}`;
      case "Model Accuracy":
        return `${(Math.random() * 0.5 + 99.5).toFixed(1)}%`;
      case "Data Processed":
        return `${(Math.random() * 0.5 + 2.2).toFixed(1)}TB`;
      case "Network Load":
        return `${Math.floor(Math.random() * 20 + 60)}%`;
      default:
        return "N/A"; // Fixed: Declared a default value
    }
  };

  const generateRandomChange = (): string => {
    const isPositive = Math.random() > 0.3;
    const value = Math.floor(Math.random() * 20 + 1);
    return `${isPositive ? "+" : "-"}${value}${Math.random() > 0.5 ? "%" : ""}`;
  };

  return (
    <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
      {metrics.map((metric, index) => (
        <motion.div
          key={metric.label}
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5, delay: index * 0.1 }}
        >
          <Card className="p-6 bg-white/80 backdrop-blur-sm border-0 shadow-lg hover:shadow-xl transition-all duration-300 group">
            <div className="flex items-center justify-between mb-4">
              <div
                className={`${metric.color} group-hover:scale-110 transition-transform duration-200`}
              >
                {metric.icon}
              </div>
              <motion.div
                key={metric.change}
                initial={{ scale: 1.2, opacity: 0 }}
                animate={{ scale: 1, opacity: 1 }}
                className={`text-sm font-medium ${
                  metric.change.startsWith("+")
                    ? "text-green-600"
                    : "text-red-600"
                }`}
              >
                {metric.change}
              </motion.div>
            </div>
            <motion.div
              key={metric.value}
              initial={{ scale: 1.1, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              className="text-2xl font-bold text-gray-800 mb-1"
            >
              {metric.value}
            </motion.div>
            <div className="text-sm text-gray-600">{metric.label}</div>
            <div className="mt-3 h-1 bg-gray-200 rounded-full overflow-hidden">
              <motion.div
                className={`h-full bg-gradient-to-r from-${
                  metric.color.split("-")[1]
                }-400 to-${metric.color.split("-")[1]}-600`}
                initial={{ width: 0 }}
                animate={{ width: `${Math.random() * 40 + 60}%` }}
                transition={{ duration: 1, delay: index * 0.1 }}
              />
            </div>
          </Card>
        </motion.div>
      ))}
    </div>
  );
}
