"use client";

import type React from "react";

import { useState, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import {
  FileText,
  ImageIcon,
  Music,
  Code,
  Camera,
  Sparkles,
  Play,
  Pause,
  Volume2,
} from "lucide-react";

interface Capability {
  id: string;
  title: string;
  description: string;
  icon: React.ReactNode;
  color: string;
  demo: React.ReactNode;
}

export default function AICapabilityShowcase() {
  const [activeCapability, setActiveCapability] = useState(0);
  const [isPlaying, setIsPlaying] = useState(false);

  const capabilities: Capability[] = [
    {
      id: "text",
      title: "Text Generation",
      description: "Advanced natural language processing and generation",
      icon: <FileText className="h-6 w-6" />,
      color: "from-blue-500 to-blue-600",
      demo: (
        <div className="space-y-3">
          <div className="bg-gray-50 p-4 rounded-lg">
            <p className="text-sm text-gray-600 mb-2">Input:</p>
            <p className="text-gray-800">"Write a haiku about AI"</p>
          </div>
          <motion.div
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            className="bg-blue-50 p-4 rounded-lg border-l-4 border-blue-500"
          >
            <p className="text-sm text-blue-600 mb-2">AI Output:</p>
            <div className="text-gray-800 font-mono">
              <p>Silicon dreams awake,</p>
              <p>Algorithms dance with thought—</p>
              <p>Future speaks in code.</p>
            </div>
          </motion.div>
        </div>
      ),
    },
    {
      id: "image",
      title: "Image Analysis",
      description: "Computer vision and image understanding",
      icon: <ImageIcon className="h-6 w-6" />,
      color: "from-purple-500 to-purple-600",
      demo: (
        <div className="space-y-3">
          <div className="bg-gray-50 p-4 rounded-lg">
            <div className="w-full h-32 bg-gradient-to-br from-purple-200 to-pink-200 rounded-lg flex items-center justify-center">
              <Camera className="h-12 w-12 text-purple-600" />
            </div>
          </div>
          <motion.div
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            className="bg-purple-50 p-4 rounded-lg border-l-4 border-purple-500"
          >
            <p className="text-sm text-purple-600 mb-2">Analysis:</p>
            <ul className="text-gray-800 text-sm space-y-1">
              <li>• Objects detected: Camera, Gradient background</li>
              <li>• Colors: Purple, Pink, White</li>
              <li>• Style: Modern, Minimalist</li>
              <li>• Confidence: 97.3%</li>
            </ul>
          </motion.div>
        </div>
      ),
    },
    {
      id: "audio",
      title: "Audio Processing",
      description: "Speech recognition and audio analysis",
      icon: <Music className="h-6 w-6" />,
      color: "from-green-500 to-green-600",
      demo: (
        <div className="space-y-3">
          <div className="bg-gray-50 p-4 rounded-lg">
            <div className="flex items-center space-x-4">
              <Button
                size="sm"
                variant="outline"
                onClick={() => setIsPlaying(!isPlaying)}
                className="flex items-center space-x-2"
              >
                {isPlaying ? (
                  <Pause className="h-4 w-4" />
                ) : (
                  <Play className="h-4 w-4" />
                )}
                <span>{isPlaying ? "Pause" : "Play"}</span>
              </Button>
              <div className="flex-1 h-2 bg-gray-200 rounded-full overflow-hidden">
                <motion.div
                  className="h-full bg-green-500"
                  animate={{ width: isPlaying ? "100%" : "0%" }}
                  transition={{ duration: 3, ease: "linear" }}
                />
              </div>
              <Volume2 className="h-4 w-4 text-gray-500" />
            </div>
          </div>
          <motion.div
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            className="bg-green-50 p-4 rounded-lg border-l-4 border-green-500"
          >
            <p className="text-sm text-green-600 mb-2">Transcription:</p>
            <p className="text-gray-800">
              "Hello, this is a demonstration of our AI audio processing
              capabilities."
            </p>
            <div className="mt-2 text-sm text-gray-600">
              <p>Language: English • Confidence: 98.7% • Speaker: Male</p>
            </div>
          </motion.div>
        </div>
      ),
    },
    {
      id: "code",
      title: "Code Generation",
      description: "Programming assistance and code analysis",
      icon: <Code className="h-6 w-6" />,
      color: "from-orange-500 to-orange-600",
      demo: (
        <div className="space-y-3">
          <div className="bg-gray-50 p-4 rounded-lg">
            <p className="text-sm text-gray-600 mb-2">Request:</p>
            <p className="text-gray-800">
              "Create a React component for a button"
            </p>
          </div>
          <motion.div
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            className="bg-orange-50 p-4 rounded-lg border-l-4 border-orange-500"
          >
            <p className="text-sm text-orange-600 mb-2">Generated Code:</p>
            <pre className="text-xs text-green-400 bg-gray-900 p-3 rounded overflow-x-auto">
              {`function Button({ children, onClick }) {
      return (
        <button 
          onClick={onClick}
          className="px-4 py-2 bg-blue-500 
                     text-white rounded hover:bg-blue-600"
        >
          {children}
        </button>
      );
    }`}
            </pre>
          </motion.div>
        </div>
      ),
    },
  ];
  // Auto-cycle through capabilities
  useEffect(() => {
    const interval = setInterval(() => {
      setActiveCapability((prev) => (prev + 1) % capabilities.length);
    }, 5000);

    return () => clearInterval(interval);
  }, [capabilities.length]);

  return (
    <div className="grid lg:grid-cols-2 gap-8 items-start">
      {/* Capability Selector */}
      <div className="space-y-4">
        {capabilities.map((capability, index) => (
          <motion.div
            key={capability.id}
            initial={{ opacity: 0, x: -20 }}
            whileInView={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.5, delay: index * 0.1 }}
          >
            <Card
              className={`p-6 cursor-pointer transition-all duration-300 border-2 ${
                activeCapability === index
                  ? "border-sky-500 bg-sky-50 shadow-lg"
                  : "border-gray-200 hover:border-gray-300 hover:shadow-md"
              }`}
              onClick={() => setActiveCapability(index)}
            >
              <div className="flex items-center space-x-4">
                <div
                  className={`p-3 rounded-lg bg-gradient-to-r ${
                    capability.color
                  } text-white ${
                    activeCapability === index ? "scale-110" : ""
                  } transition-transform duration-200`}
                >
                  {capability.icon}
                </div>
                <div className="flex-1">
                  <h3 className="text-lg font-semibold text-gray-800 mb-1">
                    {capability.title}
                  </h3>
                  <p className="text-gray-600 text-sm">
                    {capability.description}
                  </p>
                </div>
                {activeCapability === index && (
                  <motion.div
                    initial={{ scale: 0 }}
                    animate={{ scale: 1 }}
                    className="w-3 h-3 bg-sky-500 rounded-full"
                  />
                )}
              </div>
            </Card>
          </motion.div>
        ))}
      </div>

      {/* Demo Area */}
      <div className="lg:sticky lg:top-8">
        <Card className="p-6 bg-white shadow-xl border-0">
          <div className="flex items-center space-x-3 mb-6">
            <div
              className={`p-2 rounded-lg bg-gradient-to-r ${capabilities[activeCapability].color} text-white`}
            >
              {capabilities[activeCapability].icon}
            </div>
            <div>
              <h3 className="text-xl font-semibold text-gray-800">
                {capabilities[activeCapability].title}
              </h3>
              <p className="text-gray-600 text-sm">Live Demo</p>
            </div>
            <div className="ml-auto">
              <Sparkles className="h-5 w-5 text-sky-500 animate-pulse" />
            </div>
          </div>

          <AnimatePresence mode="wait">
            <motion.div
              key={activeCapability}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              transition={{ duration: 0.3 }}
            >
              {capabilities[activeCapability].demo}
            </motion.div>
          </AnimatePresence>

          {/* Progress Indicator */}
          <div className="mt-6 flex space-x-2">
            {capabilities.map((_, index) => (
              <div
                key={index}
                className={`h-1 flex-1 rounded-full transition-colors duration-300 ${
                  index === activeCapability ? "bg-sky-500" : "bg-gray-200"
                }`}
              />
            ))}
          </div>
        </Card>
      </div>
    </div>
  );
}
